/* Arrows */

/* Set class for coloring arrows -> class is active only if icon is not multi colored */

.slick-prev__custom-icon,
.slick-next__custom-icon {
    svg {
        fill: $brand-tone25 !important;
    }
    i {
        color: $brand-tone25 !important;
    }
}

.slick-prev,
.slick-next {
    position: absolute;
    display: block;
    height: 48px;
    width: 48px;
    // line-height: 0px;
    // font-size: 0px;
    cursor: pointer;
    background: transparent;
    // color: transparent;
    top: 50%;
    margin-top: -10px '\9'; /*lte IE 8*/
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    padding: 0;
    border: none;
    // outline: none;
    &:hover,
    &:focus {
        outline: none;
        background: transparent;
        // color: transparent;
        opacity: $slick-opacity-on-hover;
        // &:before {
        //     opacity: $slick-opacity-on-hover;
        // }
    }
    &.slick-disabled/* :before */ {
        opacity: $slick-opacity-not-active;
    }
}

.slick-prev:before,
.slick-next:before {
    font-family: $slick-font-family;
    font-size: 20px;
    line-height: 1;
    color: $slick-arrow-color;
    opacity: $slick-opacity-default;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
    z-index: 2;
    left: 0;
    &[dir='rtl'] {
        left: auto;
        right: 0;
    }
    // &:before {
    //     content: $slick-prev-character;
    //     &[dir="rtl"] {
    //         content: $slick-next-character;
    //     }
    // }
}

.slick-next {
    z-index: 2;
    right: 0;
    &[dir='rtl'] {
        left: 0;
        right: auto;
    }
    // &:before {
    //     content: $slick-next-character;
    //     &[dir="rtl"] {
    //         content: $slick-prev-character;
    //     }
    // }
}

/* Dots */

// .slick-slider {
//     /*margin-bottom: 30px;*/
// }

.slick-dotted {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: -45px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            cursor: pointer;
            &:hover,
            &:focus {
                outline: none;
                &:before {
                    opacity: $slick-opacity-on-hover;
                }
            }
            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: $slick-dot-character;
                width: 20px;
                height: 20px;
                font-family: $slick-font-family;
                font-size: $slick-dot-size;
                line-height: 20px;
                text-align: center;
                color: $slick-dot-color;
                opacity: $slick-opacity-not-active;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }
        &.slick-active button:before {
            color: $slick-dot-color-active;
            opacity: $slick-opacity-default;
        }
    }
}

