.dropdown-toggle,
[data-toggle='collapse'] {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    gap: 0.2em;
    color: inherit;
    white-space: initial;
    cursor: pointer;

    .icon-content:last-child {
        margin-left: auto;
    }

    &[aria-expanded='true'] {
        .icon-content--toggle {
            transform: rotate(180deg);

            &:has([href='#icon-caretRight']) {
                transform: rotate(90deg);
            }
        }
    }
}

// hide lazy loaded style elements
.dropdown-menu,
.offcanvas-nav--menu,
.modal {
    display: none; // none by default, but block on "open" of the menu
}

.jtl-spinner {
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;

    .icon-content {
        position: sticky;
        display: flex;
        justify-content: center;
        width: 100%;
        top: calc(50% - 40px);
        padding: 30px;
        font-size: 80px;
        color: #525252;
        z-index: 100000;
    }
}

.bootstrap-select {
    &.show > .dropdown-toggle.bs-placeholder,
    & > .dropdown-toggle.bs-placeholder:hover,
    & > .dropdown-toggle.bs-placeholder:focus {
        background-color: $dropdown-link-hover-bg;
    }
}

.yt-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: none;
    }

    .give-consent-preview {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        padding: 10%;
        text-decoration: none;
        background-size: cover;
        background-position: center;

        &:not(:hover) {
            color: #fff;
        }
    }
}

#consent-manager:not(.active) {
    display: none;
}
